import React, { useContext } from "react";
import { Link } from "react-router-dom";
import { GlobalState } from "../../../../GlobalState";
import SellerDesc from "../../sellerDescription/SellerDesc";
import Sold from "../sell/Sold";
function BtnRender({ product, deleteProduct, sold }) {
  //for Cart
  const state = useContext(GlobalState);
  const Id = state.userAPI.Id;
  // const addCart = state.userAPI.addCart;
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [open2, setOpen2] = React.useState(false);
  const handleOpen2 = () => setOpen2(true);
  const handleClose2 = () => setOpen2(false);

  return (
    <div className="row_btn">
      {Id === product.seller_id ? (
        <>
          {sold ? (
            <>
              <Link id="btn_buy" to="#!">
                Sold!
              </Link>
              <Link id="btn_view" to={`/detail/${product._id}`}>
                View
              </Link>
            </>
          ) : (
            <>
              <Link
                id="btn_buy"
                to="#!"
                onClick={() =>
                  deleteProduct(product._id, product.images.public_id)
                }
              >
                Delete
              </Link>
              <Link id="btn_view" to={`/edit_product/${product._id}`}>
                Edit
              </Link>
              <Link id="btn_buy" to="#!" onClick={handleOpen2}>
                Sold?
              </Link>
              <Sold
                handleClose={handleClose2}
                open={open2}
                product={product}
                id={product.seller_id}
              />
            </>
          )}
        </>
      ) : (
        <>
          <Link id="btn_buy" to="#!" onClick={handleOpen}>
            Buy
          </Link>
          <SellerDesc
            handleClose={handleClose}
            open={open}
            id={product.seller_id}
          />
          <Link id="btn_view" to={`/detail/${product._id}`}>
            View
          </Link>
        </>
      )}
    </div>
  );
}
export default BtnRender;
