import React, { useContext, useEffect, useState } from "react";
import { GlobalState } from "../../../GlobalState";
import Loading from "../utils/loading/Loading";
import axios from "axios";

function Orders() {
  const state = useContext(GlobalState);
  const [token] = state.token;
  const [callback, setCallBack] = state.productsAPI.callback;
  const [callback2, setCallBack2] = state.userAPI.callback2;
  const [loading, setLoading] = useState(false);
  const [orders, setOrders] = useState([]);

  useEffect(() => {
    try {
      const sales = async () => {
        const res = await axios.get(`/user/orders`, {
          headers: { Authorization: token },
        });
        console.log(res.data.orders);
        setOrders(res.data.orders);
      };
      sales();
    } catch (error) {
      console.log(error.response.data.msg);
    }
  }, [callback, callback2, token]);
  if (orders.length === 0) return <Loading />;

  return (
    <>
      {orders.map((order, index) => {
        return (
          <>
            <div key={order._id}>
              <h2>Order {index + 1}</h2>
              <table>
                <thead>
                  <h3>Seller Details</h3>
                </thead>
                <tr>
                  <th>User_id</th>
                  <td style={{ textTransform: "lowercase" }}>
                    {order.seller.user_id}
                  </td>
                </tr>
                <tr>
                  <th>Name</th>
                  <td>{order.seller.name}</td>
                </tr>
                <tr>
                  <th>Address</th>
                  <td>{order.seller.address}</td>
                </tr>
                <tr>
                  <th>Phone</th>
                  <td>{order.seller.phone}</td>
                </tr>
                <tr>
                  <th>Email</th>
                  <td style={{ textTransform: "lowercase" }}>
                    {order.seller.email}
                  </td>
                </tr>
              </table>
              <br />
              <table>
                <thead>
                  <h3>Product Details</h3>
                </thead>

                <tr>
                  <th>Image</th>
                  <td>
                    <img
                      src={order.product.images.url}
                      style={{ height: "70px" }}
                      alt=""
                    />
                  </td>
                </tr>
                <tr>
                  <th>Title</th>
                  <td>{order.product.title}</td>
                </tr>
                <tr>
                  <th>Product_ID</th>
                  <td>{order.product.product_id}</td>
                </tr>
                <tr>
                  <th>Price</th>
                  <td>{order.product.price}</td>
                </tr>
              </table>

              <hr />
            </div>
          </>
        );
      })}
    </>
  );
}

export default Orders;
