import { useEffect, useState } from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { toast } from "react-toastify";
import axios from "axios";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};
const styleData = {
  textTransform: "lowercase",
};
const styleHead = {
  fontWeight: "bolder",
};
export default function BasicModal({ handleClose, open, id }) {
  const [seller, setSeller] = useState();

  useEffect(() => {
    const getSeller = async () => {
      try {
        const res = await axios.get(`/user/infor/${id}`);
        if (!res) {
          toast("There seems to be an issue.Please come back later!");
        }
        setSeller(res.data);
      } catch (error) {
        toast.error(error.response.data.msg);
      }
    };
    getSeller();
  }, [id]);
  if (!seller) return null;
  return (
    <>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <center>
          <Box sx={style}>
            <Typography id="modal-modal-title" component={"span"} variant="h6">
              Contact Details-Seller
            </Typography>
            <Typography
              id="modal-modal-description"
              component={"span"}
              sx={{ mt: 2 }}
            >
              <div>
                <table>
                  <tbody>
                    <tr>
                      <td style={styleHead}>Seller ID</td>
                      <td style={styleData}>{seller.user_id.toLowerCase()}</td>
                    </tr>
                    <tr>
                      <td style={styleHead}>Name</td>
                      <td>{seller.name}</td>
                    </tr>
                    <tr>
                      <td style={styleHead}>Email</td>
                      <td style={styleData}>{seller.email}</td>
                    </tr>
                    <tr>
                      <td style={styleHead}>Address</td>
                      <td>{seller.address}</td>
                    </tr>
                    <tr>
                      <td style={styleHead}>Phone</td>
                      <td>{seller.phone}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </Typography>
          </Box>
        </center>
      </Modal>
    </>
  );
}
