import { useContext, useEffect, useState } from "react";
import Box from "@mui/material/Box";
// import Button from '@mui/material/Button';
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import { toast } from "react-toastify";
import { GlobalState } from "../../../../GlobalState";

import axios from "axios";
// import { Button } from "@mui/material";
import "./Sold.css";
const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

export default function BasicModal({ handleClose, open, id, product }) {
  const state = useContext(GlobalState);
  const [token] = state.token;
  const [callback2, setCallback2] = state.userAPI.callback2;
  const [seller, setSeller] = useState();
  const [customer_id, setCustomer_id] = useState("");
  const [callback, setCallBack] = state.productsAPI.callback;
  const [productid, setProductid] = useState(product.product_id);
  const handleSubmit = async (e) => {
    e.preventDefault()
    try {
      const res = await axios.post(
        "/api/history",
        { product_id: productid, customer_id: customer_id },
        {
          headers: { Authorization: token },
        }
      );
        console.log(res);
      if (res.data.success === true) toast.success(res.data.msg);
      setCallback2(!callback2);
      setCallBack(!callback);
    } catch (error) {
      console.log(error);
      toast.error(error.response.data.msg);
    }
  };

  useEffect(() => {
    const getSeller = async () => {
      try {
        const res = await axios.get(`/user/infor/${id}`);
        if (!res) {
          toast.info("There seems to be an issue.Please come back later!");
        }
        setSeller(res.data);
      } catch (error) {
        toast.error(error.response.data.msg);
      }
    };
    getSeller();
  }, [id]);
  if (!seller) return null;
  return (
    <>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <center>
          <Box sx={style}>
            <Typography id="modal-modal-title" variant="h6" component={"span"}>
              Transaction Details Form
            </Typography>
            <Typography
              id="modal-modal-description"
              component={"span"}
              sx={{ mt: 2 }}
            >
              <div className="transaction">
                <form onSubmit={e=>handleSubmit(e)}>
                  <div className="row">
                    <label htmlFor="customer_id">Enter the Customer ID</label>
                    <input
                      type="text"
                      name="customer_id"
                      id="customer_id"
                      required
                      value={customer_id}
                      onChange={(e) => setCustomer_id(e.target.value)}
                    />
                  </div>

                  <button type="submit">Submit</button>
                </form>
              </div>
            </Typography>
          </Box>
        </center>
      </Modal>
    </>
  );
}
