import React, { useContext, useEffect, useState } from "react";
import { GlobalState } from "../../../GlobalState";
import Loading from "../utils/loading/Loading";
import axios from "axios";

function Sales() {
  const state = useContext(GlobalState);
  const [token] = state.token;
  const [callback, setCallBack] = state.productsAPI.callback;
  const [callback2, setCallBack2] = state.userAPI.callback2;
  const [loading, setLoading] = useState(false);
  const [sales, setSales] = useState([]);

  useEffect(() => {
    try {
      const sales = async () => {
        const res = await axios.get(`/user/sales`, {
          headers: { Authorization: token },
        });
        console.log(res.data.sales);
        setSales(res.data.sales);
      };
      sales();
    } catch (error) {
      console.log(error.response.data.msg);
    }
  }, [callback, callback2, token]);
  if (sales.length === 0) return <Loading />;

  return (
    <>
      {sales.map((sale, index) => {
        return (
          <>
            <div key={sale._id}>
              <h2>Sale {index + 1}</h2>
              <table>
                <thead>
                  <h3>Customer Details</h3>
                </thead>

                <tr>
                  <th>User_id</th>
                  <td style={{ textTransform: "lowercase" }}>
                    {sale.customer.user_id}
                  </td>
                </tr>
                <tr>
                  <th>Name</th>
                  <td>{sale.customer.name}</td>
                </tr>
                <tr>
                  <th>Address</th>
                  <td>{sale.customer.address}</td>
                </tr>
                <tr>
                  <th>Phone</th>
                  <td>{sale.customer.phone}</td>
                </tr>
                <tr>
                  <th>Email</th>
                  <td style={{ textTransform: "lowercase" }}>
                    {" "}
                    {sale.customer.email}
                  </td>
                </tr>
              </table>
              <br />
              <table>
                <thead>
                  <h3>Product Details</h3>
                </thead>

                <tr>
                  <th>Image</th>
                  <td>
                    {" "}
                    <img
                      src={sale.product.images.url}
                      style={{ height: "70px" }}
                      alt=""
                    />
                  </td>
                </tr>
                <tr>
                  <th>Title</th>
                  <td>{sale.product.title}</td>
                </tr>
                <tr>
                  <th>Product_ID</th>
                  <td>{sale.product.product_id}</td>
                </tr>
                <tr>
                  <th>Price</th>
                  <td>{sale.product.price}</td>
                </tr>
              </table>

              <hr />
            </div>
          </>
        );
      })}
    </>
  );
}

export default Sales;
