import React, { useContext, useEffect, useState } from "react";
import { GlobalState } from "../../../GlobalState";
import ProductItem from "../utils/productItem/ProductItem";
import Loading from "../utils/loading/Loading";
import axios from "axios";
import BasicTabs from "./Tabs";


function MyProducts() {
  const state = useContext(GlobalState);
  const [myProducts,setMyProducts] = state.userAPI.myProducts;
  const [callback2,setCallback2] = state.userAPI.callback2;


  const [token] = state.token;
  const [callback, setCallBack] = state.productsAPI.callback;
  const [loading, setLoading] = useState(false);
  // const [isCheck, setIsCheck] = useState(false);

  // const handleCheck = (id) => {
  //   products.forEach((product) => {
  //     if (product._id === id) product.checked = !product.checked;
  //   });
  //   setProducts([...products]);
  // };

  const deleteProduct = async (id, public_id) => {
    try {
      setLoading(true);
      const destroyImg = axios.post(
        "/api/destroy",
        { public_id },
        {
          headers: { Authorization: token },
        }
      );
      const deleteProduct = axios.delete(`/api/products/${id}`, {
        headers: { Authorization: token },
      });

      await destroyImg;
      await deleteProduct;
      setCallBack(!callback);
      setCallback2(!callback2);
      setLoading(false);
    } catch (err) {
      alert(err.response.data.msg);
    }
  };

  // const checkAll = () => {
  //   products.forEach((product) => {
  //     product.checked = !isCheck;
  //   });
  //   setProducts([...products]);
  //   setIsCheck(!isCheck);
  // };

  // const deleteAll = () => {
  //   products.forEach((product) => {
  //     if (product.checked === true)
  //       deleteProduct(product._id, product.images.public_id);
  //   });
  // };
  if (loading)
    return (
      <div>
        <Loading />
      </div>
    );
  return (
    <>
      {/* {isAdmin && (
        <div className="delete-all">
          <span>Select all</span>
          <input type="checkbox" checked={isCheck} onChange={checkAll} />
          <button onClick={deleteAll}>Delete ALL</button>
        </div>
      )} */}
      <div width="100%"
      >
        <BasicTabs myProducts={myProducts} deleteProduct={deleteProduct} width="100%"/>
      </div>

      {myProducts.length === 0 && <h1>Create Products(Link in the navbar)</h1>}
    </>
  );
}

export default MyProducts;
