import axios from "axios";
import { useState, useEffect } from "react";
import { toast } from "react-toastify";

function UserAPI(token) {
  const [isLogged, setIsLogged] = useState(false);
  const [isAdmin, setIsAdmin] = useState(false);
  const [callback2, setCallback2] = useState(false);
  const [history, setHistory] = useState([]);
  const [Id, setId] = useState(false);
  const [myProducts, setMyProducts] = useState([]);
  useEffect(() => {
    if (token) {
      const getUser = async () => {
        try {
          const res = await axios.get("/user/infor", {
            headers: { Authorization: token },
          });
          setIsLogged(true);
          res.data.role === 1 ? setIsAdmin(true) : setIsAdmin(false);
          setId(res.data._id);
          // setCart(res.data.cart);
        } catch (err) {
          console.log(err.response);
          console.log(err.response.data.msg);
        }
      };
      getUser();
    }
  }, [token, callback2]);
  useEffect(()=>{
    if(token){
        const getMyProducts = async ()=>{
            try {
                const res = await axios.get(`/api/products/${Id}`,{
                    headers:{Authorization:token}
                })
                setMyProducts(res.data.products);
                // setCart(res.data.cart);
                // console.log(res);
            } catch (err) {
                toast.error(err.response.data.msg);
            }
        }
        getMyProducts();

    }
  },[Id,callback2])

  // const addCart = async (product)=>{
  //     if(!isLogged) return toast.info(`Please login to continue buying`);

  //     const check = cart.every(item=>{
  //         return item._id !== product._id;
  //     })

  //     if(check){
  //         setCart([...cart,{...product,quantity:1}]);

  //             await axios.patch('/user/addcart',{cart:[...cart,{...product,quantity:1}]},{
  //                 headers:{Authorization:token}
  //             })

  //     }else{
  //         toast.warn("This product has been already added to cart.")
  //     }
  // }

  return {
    isLogged: [isLogged, setIsLogged],
    isAdmin: [isAdmin, setIsAdmin],
    Id,
    callback2: [callback2, setCallback2],
    // addCart: addCart,
    myProducts: [myProducts, setMyProducts],
    history: [history, setHistory],
  };
}

export default UserAPI;
